.newUser{
    flex: 4;

}
.userForm{
    display: flex;
    flex-wrap: wrap;
}
.newUserItem{
width: 400px;
display: flex;
flex-direction: column;
margin-right: 20px;
margin-top: 10px;
}
.newUserItem>label{
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 600;
    color: rgb(160, 157, 157);
}
.newUserItem>input{
    height: 20px;
    padding: 10px;
    border: 1px solid gray;
    border-radius: 5px;
}
.newUserGender>input{
    margin-top: 15px;
}
.newUserGender>label{
    margin: 10px;
    font-size: 18px;
    color: #555;
}
.newUserSelect{
    height: 40px;
    border-radius: 5px;
}
.newUserButton{
    width: 200px;
    border: none;
    color: white;
    background-color: darkblue;
    padding: 7px 10px;
    font-weight: 600;
    border-radius: 10px;
    margin-top: 30px;
    cursor: pointer;

}